\n//   padding: 14px 40px;\n//   font-size: 16px;\n//   cursor: pointer;\n\n//   &:active {\n//     transform: scale(0.98);\n//   }\n\n//   &:focus {\n//     outline: 0;\n//   }\n// }\n\n","/* Box sizing rules */\n*,\n*::before,\n*::after {\n  box-sizing: border-box;\n}\n\n/* Remove default margin */\nbody,\nh1,\nh2,\nh3,\nh4,\np,\nfigure,\nblockquote,\ndl,\ndd {\n  margin: 0;\n}\n\n/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */\nul,\nol {\n  list-style: none;\n}\n\n/* Set core body defaults */\nbody {\n  min-height: 100vh;\n  text-rendering: optimizeSpeed;\n  line-height: 1.5;\n}\n\n/* A elements that don't have a class get default styles */\na:not([class]) {\n  text-decoration-skip-ink: auto;\n}\n\n/* Make images easier to work with */\nimg,\npicture {\n  max-width: 100%;\n  display: block;\n}\n\n/* Inherit fonts for inputs and buttons */\ninput,\nbutton,\ntextarea,\nselect {\n  font: inherit;\n}\n\n/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */\n@media (prefers-reduced-motion: reduce) {\n  html:focus-within {\n   scroll-behavior: auto;\n  }\n  \n  *,\n  *::before,\n  *::after {\n    animation-duration: 0.01ms !important;\n    animation-iteration-count: 1 !important;\n    transition-duration: 0.01ms !important;\n    scroll-behavior: auto !important;\n  }\n}\n","$primary-color: #20232A;\n$secondary-color: #ACBEBE;\n$tertiary-color: #F4F4EF;\n$highlights: #A01D26;\n\n.nav {\n    min-width: 980px;\n}\n.main-nav {\n    display: grid;\n    grid-template-columns: 1fr 180px 1fr 620px 1fr 180px 1fr;\n    justify-content: space-between;\n    margin: 0 auto;\n    background-color: #ACBEBE;\n}\n\n.main-title {\n    display: flex;\n    grid-column: 4;\n    justify-content: center;\n}\n\n.main-nav > ul {\n    display: flex;\n    flex-direction: row;\n    justify-content: end;\n    align-items: center;\n\n}\n\n.grow:hover {\n    transition-duration: 300ms;\n    transform: scale(1.1);\n}\n\n.div-box {\n    display: grid;\n    grid-template-columns: repeat(1fr, 2);\n    grid-template-rows: repeat(1fr, 2);\n    grid-column: 2;\n    width: 100%;\n    margin: auto;\n\n    .music1 {\n        grid-column: 1;\n        grid-row: 1;\n        width: 45px;\n        height: 45px;\n        padding: 0;\n        border: none;\n        background: none;\n        margin: auto;\n\n        img {\n            margin: auto auto;\n            width: 24px;\n            height: 24px;\n        }\n    }\n\n    .music2 {\n        grid-column: 1;\n        grid-row: 1;\n        width: 45px;\n        height: 45px;\n        padding: 0;\n        border: none;\n        background: none;\n        margin: auto;\n\n        img {\n            margin: auto auto;\n            width: 33px;\n            height: 33px;\n        }\n    }\n\n    .volume-up {\n        grid-column: 2;\n        grid-row: 1;\n        width: 45px;\n        height: 45px;\n        padding: 0;\n        border: none;\n        background: none;\n\n        img {\n            width: 30px;\n            height: 30px;\n            margin: auto;\n        }\n    }\n\n    .volume-down {\n        grid-column: 2;\n        grid-row: 2;\n        width: 45px;\n        height: 45px;\n        padding: 0;\n        border: none;\n        background: none;\n\n        img {\n            width: 30px;\n            height: 30px;\n            margin: auto;\n        }\n    }\n\n    .sound {\n        grid-column: 1;\n        grid-row: 2;\n    }\n}\n\n.logo-list {\n    grid-column: 6;\n}\n\n.main-nav li {\n    margin: 10px;\n    text-decoration: none;\n}\n\na {\n    text-decoration: none;\n    color: #20232A;\n}\n\n.main-nav h2 {\n    margin: 20px;\n}\n\n.logo {\n    width: 34px;\n    height: 34px;\n    margin-right: 20px;\n}\n\n@media screen and (min-width: 1920px) {\n\n    .main-nav {\n        display: grid;\n        grid-template-columns: 1fr 180px 235px 620px 235px 180px 1fr;\n        justify-content: space-between;\n        margin: 0 auto;\n        background-color: #ACBEBE;\n    }\n}","$primary-color: #2c4a52;\n$secondary-color: #ddfafa;\n$tertiary-color: #e4e4da;\n$highlights: #A01D26;\n\n.resources-bar {\n    display: flex;\n    justify-content: space-between;\n    margin: 0 auto;\n    background: -webkit-linear-gradient($secondary-color,$primary-color);\n    opacity: .7;\n  \n}\n\n.resources-bar > ul {\n    // display: flex;\n    flex-direction: row;\n    justify-content: end;\n    align-items: flex-end;\n\n}\n\n.rss-left {\n    // justify-content: space-between;\n    width: 50%;\n    text-align: center;\n}\n\n.rss-right {\n    // justify-content: end;\n    width: 50%;\n    text-align: center;\n}\n\n.rss-left > ul {\n    display: flex;\n    flex-direction: row;\n    justify-content: end;\n\n    li {\n        padding: 10px;\n    }\n\n}\n\n.rss-right > ul {\n    display: flex;\n    flex-direction: row;\n    // justify-content: end;\n\n    li {\n        padding: 10px;\n    }\n\n}\n",".tutorial-holder1 {\n    display: flex;\n    justify-content: center;\n    grid-column: 1/8;\n    grid-row: 1;\n    z-index: 2;\n    width: 100%;\n    height: 100%;\n    \n    #box-wrap {\n        display: flex;\n        justify-content: space-between;\n        align-items: center;\n        flex-direction: column;\n        padding-top: 10px;\n        margin: auto auto;\n        margin-top: 100px;\n        height: 200px;\n        width: 600px;\n        background: #fff;\n\n        #tutorial-text {\n            padding: 10px;\n            padding-left: 30px;\n            padding-right: 30px;\n        }\n\n        p {\n            margin-left: 30px;\n            margin-right: 30px;\n        }\n\n        #tut-button1 {\n            height: 40px;\n            width: 80px;\n            justify-content: center;\n            margin-bottom: 10px;\n\n        }\n\n        #tut-button5 {\n            height: 40px;\n            width: 100px;\n            justify-content: center;\n            margin-bottom: 10px;\n\n        }\n    }\n\n}\n\n.tutorial-holder2 {\n    display: flex;\n    justify-content: center;\n    grid-column: 1/8;\n    grid-row: 1;\n    z-index: 2;\n    width: 100%;\n    height: 100%;\n    \n    #box-wrap {\n        display: flex;\n        justify-content: space-between;\n        align-items: center;\n        flex-direction: column;\n        padding-top: 10px;\n        margin-right: 40%;\n        margin-top: 100px;\n        height: 200px;\n        width: 600px;\n        background: #fff;\n\n        #tutorial-text {\n            padding: 10px;\n            padding-left: 30px;\n            padding-right: 30px;\n        }\n\n        p {\n            margin-left: 30px;\n            margin-right: 30px;\n        }\n\n        #tut-button2 {\n            height: 40px;\n            width: 80px;\n            justify-content: center;\n            margin-bottom: 10px;\n\n        }\n    }\n\n}\n\n.tutorial-holder3 {\n    display: flex;\n    justify-content: center;\n    grid-column: 1/8;\n    grid-row: 1;\n    z-index: 2;\n    width: 100%;\n    height: 100%;\n    \n    #box-wrap {\n        display: flex;\n        justify-content: space-between;\n        align-items: center;\n        flex-direction: column;\n        padding-top: 10px;\n        height: 200px;\n        width: 600px;\n        background: #fff;\n\n        #tutorial-text {\n            padding: 10px;\n            padding-left: 30px;\n            padding-right: 30px;\n        }\n\n        p {\n            margin-left: 30px;\n            margin-right: 30px;\n        }\n\n        #tut-button3 {\n            height: 40px;\n            width: 80px;\n            justify-content: center;\n            margin-bottom: 10px;\n\n        }\n    }\n\n}\n\n.tutorial-holder4 {\n    display: flex;\n    justify-content: center;\n    grid-column: 1/8;\n    grid-row: 1;\n    z-index: 2;\n    width: 100%;\n    height: 100%;\n    \n    #box-wrap {\n        display: flex;\n        justify-content: space-between;\n        align-item